import React from 'react'
import './config'
import $ from 'jquery'
import 'antd-mobile/dist/antd-mobile.css'
import {ActivityIndicator,List,Button} from 'antd-mobile';

const Item = List.Item;
const Brief = Item.Brief;

class LovePage extends React.Component{
    constructor(props){
        super(props);
        this.state={
            selectedName:'',
            open:false,
            loading1:true,
            srcList: [],
            srcdetail:{},
        };
    }
    componentDidMount(){
        $.ajax({
            type:'get',
            async:true,
            url:global.constants.website+'admin/getlovelist',
            dataType:'json',
            success:(result)=> {
                var lovelist=result['LoveName']
                this.setState({
                    srcList:lovelist,
                    loading1:false,
                })
            },
            error : function(errorMsg) {
                alert("加载数据失败！");
                console.log(errorMsg);
            }
        })
    }

    showModal(srcname){
        $.ajax({
            type:'get',
            async:true,
            url:global.constants.website+'admin/getbufferdetail?name='+srcname,
            dataType:'json',
            xhrFields: {
                withCredentials: true
            },
            success:(result)=> {
                this.setState({
                    selectedName:srcname,
                    srcdetail:result,
                })
            },
            error : function(errorMsg) {
                alert("加载数据失败！");
                console.log(errorMsg);
            }
        })
    }

    render(){
        if (this.state.loading1==true){
            return (
                <div>
                <ActivityIndicator text="加载资源库中..."/>
                </div>
            );
        }
        if(this.state.loading1==false && this.state.selectedName==''){
            return (
                <div>
                    <List renderHeader={() => '我的最爱'} className="my-list">
                    {this.state.srcList.map((srcname)=>(
                        <Item onClick={()=>this.showModal(srcname)}>{srcname}</Item>
                    ))}
                    </List>

                </div>

            )
        }
        if(this.state.selectedName!=''){
            return(
                <div>
                    <Button type="warning" onClick={()=>this.setState({selectedName:''})}>返回</Button>
                    {Object.keys(this.state.srcdetail).map((key,index)=>{
                        return (
                            <List renderHeader={key} className="my-list">
                            {Object.keys(this.state.srcdetail[key]).map((ep,epindex)=>{
                                var a=this.state.srcdetail[key]
                                return(
                                    <a href={a[ep]}><Item>{ep}</Item></a>
                                )
                            })}
                            </List>
                        )
                    })}
                </div>
            )
        }
    }
}

export default LovePage