import React from 'react'

import {
  TabBar
} from 'antd-mobile'
import LovePage from "./LovePage"
import LibraryView from "./LibraryView"


import 'antd-mobile/dist/antd-mobile.css'

class TabBarExample extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'blueTab',
      hidden: false,
    };
  }



  render() {
    return (
      <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
          tabBarPosition="bottom"
          hidden={this.state.hidden}
          prerenderingSiblingsNumber={0}
        >
          <TabBar.Item
            title="影视库"
            key="Library"
            icon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(src/icon/ku-noactiv.svg) center center /  21px 21px no-repeat' }}
            />
            }
            selectedIcon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(src/icon/ku-activ.svg) center center /  21px 21px no-repeat' }}
            />
            }
            selected={this.state.selectedTab === 'blueTab'}
            badge={'new'}
            onPress={() => {
              this.setState({
                selectedTab: 'blueTab',
              });
            }}
            data-seed="logId"
          >
            <LibraryView/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(src/icon/heart-noactiv.svg) center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(src/icon/heart-activ.svg) center center /  21px 21px no-repeat' }}
              />
            }
            title="最爱"
            key="Love"
            selected={this.state.selectedTab === 'redTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'redTab',
              });
            }}
            data-seed="logId1"
          >
            <LovePage/>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(src/icon/search-noactiv.svg) center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(src/icon/search-activ.svg) center center /  21px 21px no-repeat' }}
              />
            }
            title="搜索"
            key="Search"
            dot
            selected={this.state.selectedTab === 'greenTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'greenTab',
              });
            }}
          >
            <h1>这里可以搜各种资源</h1>
          </TabBar.Item>
          <TabBar.Item
            icon={{ uri: 'src/icon/my-noactiv.svg' }}
            selectedIcon={{ uri: 'src/icon/my-activ.svg' }}
            title="我"
            key="my"
            selected={this.state.selectedTab === 'yellowTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'yellowTab',
              });
            }}
          >
            <h1>这是关于你账户的信息</h1>
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}


export default TabBarExample
